@import '../../globalVar.scss';

.modal {
	display: none;
	position: fixed;
	z-index: 1;
	padding-top: 100px;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: $backgroundModal;

	&__dialog {
		background-color: $menuBackground;
		margin: auto;
		padding: 20px;
		box-shadow: $menuShadow;
		border-radius: 12px;
		width: 360px;
		z-index: inherit;
	}
	&__header {
		margin: 20px 0;
		background-color: inherit;
		z-index: inherit;
	}
}

